:root {
  --background: #fdf6f0;
  --primary: #ff7eb6;
  --secondary: #42be65;
  --accent: #ffb703;
  --text-dark: #2e2e2e;
  --text-light: #ffffff;
  --border-radius: 12px;
  --shadow: 0 4px 12px rgba(0, 0, 0, 0.1);
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Inter', sans-serif;
}
body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  text-align: center;
  background-color: #f9f9f9;
  font-family: Arial, sans-serif;
  margin: 0;
}
.main-container {
  width: 90%;
  max-width: 600px;
  padding: 20px;
  background: white;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
}
h1 {
  text-align: center;
  font-size: 2rem;
  color: var(--primary);
}
.task-list {
  margin-top: 20px;
}
.task {
  display: center;
  align-items: center;
  justify-content: space-between;
  background: white;
  padding: 15px;
  margin-bottom: 10px;
  border-radius: var(--border-radius);
  box-shadow: var(--shadow);
  transition: transform 0.2s ease-in-out;
}
.task:hover {
  transform: scale(1.02);
}
.task-title {
  font-weight: bold;
  color: var(--text-dark);
}
.task-description {
  font-size: 0.9rem;
  color: #666;
}
.task.completed {
  background: var(--secondary);
  color: var(--text-light);
}
.task.completed .task-title {
  text-decoration: line-through;
  color: var(--text-light);
}
button {
  padding: 10px 16px;
  font-size: 1rem;
  font-weight: bold;
  border: none;
  cursor: pointer;
  border-radius: var(--border-radius);
  transition: all 0.2s ease-in-out;
}
.add-task-btn {
  background: var(--primary);
  color: var(--text-light);
  display: block;
  width: 100%;
  margin-top: 20px;
}
.add-task-btn:hover {
  background: #ff569c;
}
.complete-btn {
  background: var(--secondary);
  color: var(--text-light);
}
.complete-btn:hover {
  background: #34a853;
}
.delete-btn {
  background: var(--accent);
  color: var(--text-dark);
}
.delete-btn:hover {
  background: #ff9900;
}
input, textarea {
  width: 100%;
  padding: 10px;
  margin-top: 10px;
  border: 1px solid #ddd;
  border-radius: var(--border-radius);
  font-size: 1rem;
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.task {
  animation: fadeIn 0.5s ease-in-out;
}
